.player-wrapper {
    position: relative;
    padding-top: 56.25% /* Player ratio: 100 / (1280 / 720) */
  }
  
  .react-player {
    position: absolute;
    top: 0;
    left: 0;
  }

  @media (min-width: 600px) and (max-device-width: 768px) {
    .player-wrapper{
      margin-top:100px;
    }
  }