.toggle-button {
    display: none;
    flex-direction: column;
    justify-content: space-around;
    height: 40px;
    width: 36px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding:0;
    box-sizing: border-box;
}

.toggle-button:focus {
    outline: none;
    
}

.toggle-button-line{
    width: 30px;
    height: 3px;
    background: #4EC5C1;
}

